<template>
  <div class="protocal-list layout">
      <p class="title">{{ this.$route.query.productName}}</p>
      <p class="sub-title">主条款</p>
    <ul>
      <li v-for="(item,index) in this.list" :key="index" @click="goProtocal(item)"><span>{{item.name}}</span><i v-if="item.name"></i></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'TheProtocal',
  data () {
    return {
      list: []
    }
  },
  mounted () {
    this.getProtocal()
  },
  methods: {
    goProtocal (item) {
      window.open(`${process.env.VUE_APP_RESOURCE_URL}/${item.url}`)
    },
    getProtocal () {
      let postData = {
        id: this.$route.query.productId
      }
      this.$api.product.getProtocalList(postData).then(res => {
        this.list = res.data
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
 .protocal-list{
   min-height: 500px;
   background: #fff;
   padding:40px 0 0 40px;
   .title{
     font-size: 20px;
     color: $color-text;
     font-weight: bold;
   }
   .sub-title{
     margin-top: 20px;
     font-size: 20px;
     color: $color-text;
     padding-left:133px;
     font-weight: bold;
   }
   ul{
     padding:40px 0 0 240px;
     li{
       margin-bottom: 28px;
       span{
         font-size: 16px;
         color: $color-text;
         border-bottom: $border-color;
       }
       i{
         display: inline-block;
         width:18px;
         height:18px;
         vertical-align: top;
         background: url("../../../assets/images/icon_pdf.png") no-repeat;
         background-size: 18px 18px;
         margin-left: 20px;
       }
     }
   }
 }
</style>
